:root {
  --font: "Baloo Tamma 2", cursive;
  --main-colour: #96c6e0;
  --secondary-colour: #67acc7;
  --gradient-colour: linear-gradient(to top, #4b778f, #6c95ad);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--font);
}

.container {
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
}
.container label {
  padding-right: 5rem;
  font-size: 2rem;
  color: var(--secondary-colour);
}
.container input {
  font-size: 1.5rem;
  outline: none;
  font-family: var(--font);
  padding: 0 0.3rem;
}